import React from 'react'

export default function ProjectCard({title, description, technologies, githubUrl, projectUrl}) {
  return (
    <div className="card project-card">
      <div className="card-body">
        <h5>{title}</h5>
        <p className="card-text">{description}</p>
        <h6>Technologies</h6>
        <div className="project-tech">
          {technologies.map((current, index) => (
            <img
              key={index}
              src={current.image}
              className="img-fluid technologies-logo-circle"
              alt={current.title}
            />
          ))}
        </div>
        <div className="btn-group pt-4">
          {githubUrl && (
            <a class="btn btn-outline-dark" href={githubUrl}>
              <i class="bx bxl-github"></i> GitHub Link
            </a>
          )}
          {projectUrl && (
            <a class="btn btn-outline-dark" href={projectUrl}>
              <i class="bx bx-link-alt"></i> Project Link
            </a>
          )}
        </div>
      </div>
    </div>
  );
}
